html {
	min-width: 100vw;
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.App {
	text-align: center;
}

.App-header {
	background-color: #282c34;
	min-height: 7vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(0.65em + 2.5vmin);
	color: white;
}

.scoreboard {
	position: relative;
	margin: 0.6em 0 0 0;
	left: 68%;
	font-weight: bold;
	text-align: left;
	width: 14rem;
}

#commentary-display {
	position: relative;
	left: 68%;
	top: 10rem;
	font-weight: bold;
	text-align: left;
	width: 22rem;
}

ul {
	padding: 0;
	margin: 0;
}

#human-board > .board-squares {
	padding: 0px 0.125rem;
	border: 1px solid black;
	background-color: #bbbbbb;
	color: black;
}

#human-board > .board-squares:hover {
	background-color: black;
	color: white;
	cursor: crosshair;
}

#human-board > .hit {
	color: black;
	background-color: red;
	padding: 0px 0.125rem;
	border: 1px solid black;
}

#human-board > .miss {
	color: black;
	background-color: white;
	padding: 0px 0.125rem;
	border: 1px solid black;
}

#computer-board > .board-squares {
	background-color: #bbbbbb;
	font-size: 0.6em;
	letter-spacing: 0.5px;
	padding: 0px 0.125rem;
	border: 1px solid black;
	pointer-events: none;
}

#computer-board > .hit {
	color: black;
	background-color: red;
	padding: 0px 0.125rem;
	border: 1px solid black;
	font-size: 0.6em;
	letter-spacing: 0.5px;
}

#computer-board > .miss {
	color: black;
	background-color: white;
	padding: 0px 0.125rem;
	border: 1px solid black;
	letter-spacing: 0.5px;
	font-size: 0.6em;
}

.playerShips {
	background-color: black;
	font-size: 0.6em;
	letter-spacing: 0.5px;
	padding: 0px 0.125rem;
	border: 1px solid black;
	color: white;
	border-radius: 0.625rem;
}

.disabled {
	pointer-events: none;
}

#human-board {
	width: 22rem;
	height: 22rem;
	display: grid;
	grid-gap: 0.2rem;
	padding: 0.2rem;
	outline: 1px solid black;
	position: relative;
	grid-template-columns: repeat(10, 1fr);
	background-color: royalblue;
	cursor: crosshair;
}

#computer-board {
	width: 16rem;
	height: 16rem;
	display: grid;
	grid-gap: 0.2rem;
	padding: 0.2rem;
	outline: 1px solid black;
	position: relative;
	grid-template-columns: repeat(10, 1fr);
	background-color: royalblue;
}

.board-header {
	margin: 0;
}

.play-area {
	position: fixed;
	width: 80%;
	min-height: 100%;
	top: 4rem;
	left: 10.5rem;
	display: inline-block;
}

#human-board-wrapper {
	float: right;
	margin: 8% 35.5% 0 0;
}

#computer-board-wrapper {
	float: left;
	margin: 1% 0 0 0;
}

