#human-board > .board-squares {
	padding: 0px 0.125rem;
	border: 1px solid black;
	background-color: #bbbbbb;
	color: black;
}

#human-board > .board-squares:hover {
	background-color: black;
	color: white;
	cursor: crosshair;
}

#human-board > .hit {
	color: black;
	background-color: red;
	padding: 0px 0.125rem;
	border: 1px solid black;
}

#human-board > .miss {
	color: black;
	background-color: white;
	padding: 0px 0.125rem;
	border: 1px solid black;
}

#computer-board > .board-squares {
	background-color: #bbbbbb;
	font-size: 0.6em;
	letter-spacing: 0.5px;
	padding: 0px 0.125rem;
	border: 1px solid black;
	pointer-events: none;
}

#computer-board > .hit {
	color: black;
	background-color: red;
	padding: 0px 0.125rem;
	border: 1px solid black;
	font-size: 0.6em;
	letter-spacing: 0.5px;
}

#computer-board > .miss {
	color: black;
	background-color: white;
	padding: 0px 0.125rem;
	border: 1px solid black;
	letter-spacing: 0.5px;
	font-size: 0.6em;
}

.playerShips {
	background-color: black;
	font-size: 0.6em;
	letter-spacing: 0.5px;
	padding: 0px 0.125rem;
	border: 1px solid black;
	color: white;
	border-radius: 0.625rem;
}

.disabled {
	pointer-events: none;
}
